* {
  box-sizing: border-box;
}

body {
  color: rgba(233, 225, 225, .894);
  background-color: rgba(27, 50, 201, .25);
  font-family: Roboto, sans-serif;
}

ul {
  list-style: none;
}

a {
  height: 100%;
  text-decoration: none;
  display: block;
}

.search-form {
  z-index: 999;
  height: 70px;
  width: 100%;
  background-color: rgba(27, 50, 201, .25);
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
}

input {
  color: inherit;
  width: 300px;
  height: 40px;
  background-color: rgba(27, 50, 201, .64);
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 25px;
  padding: 0 15px;
  font-size: 20px;
}

input::-ms-input-placeholder {
  color: rgba(233, 225, 225, .894);
  align-items: center;
  font-size: 15px;
}

input::placeholder {
  color: rgba(233, 225, 225, .894);
  align-items: center;
  font-size: 15px;
}

.submit-btn {
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid rgba(0, 0, 0, 0);
  align-items: center;
  display: flex;
  position: relative;
  left: -45px;
}

.icon-search {
  width: 20px;
  height: 20px;
  opacity: .5;
}

form button:hover .icon-search {
  opacity: .8;
}

.gallery {
  flex-wrap: wrap;
  justify-content: space-around;
  gap: 15px;
  padding-top: 60px;
  display: flex;
}

.photo-card {
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  box-shadow: 0 8px 8px -5px rgba(0, 0, 0, .3);
}

.photo-card:hover {
  transform: scale(1.01);
}

img {
  object-fit: cover;
  height: 100%;
  max-width: 100%;
  display: block;
}

.wrap {
  text-align: center;
  justify-content: space-around;
  padding: 0 5px;
  font-size: 14px;
  display: flex;
}

.info {
  color: fff;
  position: relative;
  bottom: 50px;
}

.info-item {
  flex-direction: column;
  display: flex;
}

.load-more {
  width: 130px;
  height: 45px;
  color: inherit;
  opacity: 0;
  background-color: rgba(27, 50, 201, .25);
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 25px;
  margin: 30px auto;
  font-size: 18px;
  display: block;
}

.load-more:-webkit-any(:hover, :focus) {
  background-color: rgba(27, 50, 201, .455);
}

.load-more:is(:hover, :focus) {
